@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);

.app__container{box-sizing:border-box;max-width:1920px;height:100%;background-color:#fff;padding:40px 60px 0;margin:0 auto;font-family:Roboto, Helvetica, sans-serif;font-style:normal;font-weight:bold;font-size:24px;color:#000}.app__container--with-modal{overflow:hidden}.app__top{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;grid-gap:20px;gap:20px}

.logo{box-sizing:border-box}.logo__content{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.logo____img-box{width:200px;height:200px}.logo__img{width:100%}.logo__text{font-size:72px;line-height:84px;text-transform:uppercase}

.search{position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;max-width:600px}.search__icon{display:inline-block;position:absolute;right:24px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.search__input{box-sizing:border-box;width:100%;height:80px;margin:0;padding:0;outline:none;border:none;background:#FFFFFF;box-shadow:0px 2px 4px rgba(0,0,0,0.25);border-radius:20px;padding:12px 32px;font-weight:normal;font-size:48px;line-height:56px}.search__input::-webkit-input-placeholder{font-weight:normal;font-size:48px;line-height:56px;color:#B2B2B2}.search__input:-ms-input-placeholder{font-weight:normal;font-size:48px;line-height:56px;color:#B2B2B2}.search__input::placeholder{font-weight:normal;font-size:48px;line-height:56px;color:#B2B2B2}

.table{width:100%;margin:0;padding:0;line-height:80px}.table__row{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;height:80px;margin-bottom:20px;border-radius:20px;background:#fff;box-shadow:0px 2px 4px rgba(0,0,0,0.25)}.table__head{background:#2196F3;color:#fff}.table__col-1{width:78px;text-align:center}.table__col-2{width:1160px;padding-left:23px;border-left:1px solid #B2B2B2;border-right:1px solid #B2B2B2}.table__col-2--head{border-left:1px solid #fff;border-right:1px solid #fff}.table__col-3{width:560px;padding-left:23px}

.modal__body{position:fixed;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.5)}.modal__container{box-sizing:border-box;position:relative;max-width:600px;background-color:#fff;border-radius:20px;padding:20px 20px;margin:0 auto;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.modal__title{margin:0;padding:0;text-align:center;font-size:48px;line-height:56px;margin-bottom:14px}.modal__table{width:100%;line-height:28px;font-weight:normal;color:#666666;margin-bottom:12px}.modal__table-col-1{width:30px}.modal__table-col-2{padding:24px 0 24px 30px}.modal__table-col-3{text-align:right}.modal__btn{display:block;margin:0 auto;padding:10px 70px;background:#2196F3;border-radius:20px;outline:none;border:none;color:#fff;font-family:Roboto, Helvetica, sans-serif;font-style:normal;font-weight:bold;font-size:24px;cursor:pointer}

